.banner {
  /* margin-top: 10vh; */
  height: 90vh;
  margin-bottom: 90px; 
  color: white;
  object-fit: contain;
}
.banner__contents {
  margin-left: 30px;
  padding-top: 230px;
  /* height: 190px; */
}
.banner__title {
  font-size: 3rem;
  font-weight: 800;
  padding-bottom: 0.3rem;
}

.banner-description {
  width: 45rem;
  line-height: 1;
  padding: 1rem 0;
  font-size: 1rem;
  max-width: 360px;
  height: 80px;
}

.banner__button {
  cursor: pointer;
  color: #fff;
  outline: none;
  border: none;
  font-size: 1.2rem;
  font-weight: 700;
  border-radius: 0.2vw;
  padding: 1rem 3rem;
  margin-right: 2rem;
  background-color: rgb(51, 51, 51, 0.5);
}

.play {
  color: #000;
  background-color: #fff;
}

.banner__button:hover {
  color: #000;
  background-color: #c6c6c6;
  transition: all 0.2s;
}
.banner__fadeBottom {
  height: 40vh;
  background-color: linear-gradient(
    180deg,
    trasparent,
    rgb(37, 37, 37, 0.91),
    #111
  );
}
